export default {
	data() {
		return {
			result:''
		}
	},
	onLoad(){
	},
	methods: {
		back(){
			uni.navigateTo({
			    url: '../myhousetype/myhousetype'
			});
		},
		myhousetyperesult(){
			uni.navigateTo({
			    url: '../myhousetyperesult/myhousetyperesult'
			});
		},
		searchresult(e){
			this.result = e.detail.value
			uni.navigateTo({
			    url: '../myhousetyperesult/myhousetyperesult?searchresult='+this.result
			});
		}
	}
}
